/* ---------- root variables ---------- */

body {
    color: #80FFFF;
    
    
    
    padding: 0;
    margin: 0;
    font-family: 'darkmode-off', sans-serif;
    font-weight: 400;
    font-style: normal;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

a {
    text-decoration: none;
    color: #80FFFF;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}


h1 {
    font-size: 2em;
    font-weight: 500;
    letter-spacing: 0.1px;
}

h2 {
    font-weight: 500;
    letter-spacing: 0.1px;
}

h3 {
    font-weight: 500;
    letter-spacing: 0.1px;
}

p {
    margin: 0;
    padding: 0;
    border: 0;
}


.text-center {
    text-align: center;
}

.text-error {
    color: var(--text-color-danger);
}

.text-error:first-letter {
    text-transform: uppercase;
}

/* ---------- animations ---------- */
/* fade in */
@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}




/* ---------- styling for the web view---------------------------------- */


