        box-shadow: -3.5px 3.5px #80FFFF,
            -3.5px 3.5px 0px 1.5px #6919F6;
        margin: 15px;
        background-color: #172650;
 
    }

    .EV-web:active, .EV-web:hover, .EV-web:after{
